<template lang="html">
  <div id="map"></div>
</template>

<script>
import mapboxgl from 'mapbox-gl';
mapboxgl.accessToken = 'pk.eyJ1IjoiY2FydG9uZyIsImEiOiJjazJldzVobGkwOWRxM2hzNTB1M3o2cG94In0.w7FyG31FWqXm3vXSh6WtxQ';
export default {
  name: 'TheMap',
  mounted(){
    this.map = new mapboxgl.Map({
    container: 'map',
    style: 'mapbox://styles/cartong/ckewzj71k18o619tceu43dtkz',
    center: [0, 0], // starting position [lng, lat]
    zoom: 2
    });
    this.refreshMarkers()
    // this.map.on('load', this.refreshMarkers())
  },
  computed:{
    h2hData(){
      return this.$store.state.h2hData
    }
  },
  watch:{
    h2hData(){
      this.refreshMarkers()
    }
  },
  methods:{
    refreshMarkers(){
      for (let d of this.h2hData) {
        if (d.coordinates !== null) {
          d.coordinates.map(c => {
            const marker = new mapboxgl.Marker().setLngLat([Number(c[0]), Number(c[1])]).addTo(this.map)
            marker.setPopup(new mapboxgl.Popup({ offset: 25 }) // add popups
            .setHTML('<h3>' + d.projectTitle+ '</h3><p>Service Type' + d.serviceType + '</p>'))
          })
          // console.log(d.coordinates);
        }
      }
    }
  }
}
</script>

<style lang="css" scoped>
#map{
  width: 100%;
  height: 100%;
}
</style>
