import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios'

Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    h2hData: []
  },
  mutations: {
    setH2HData(state, data){
      state.h2hData = data
    }
  },
  actions: {
    async getData(context){
      const rawData = await axios.get('https://spreadsheets.google.com/feeds/list/14peMjjAQN8pGtUbbL-8mUHqYYffFrBG2axYHb5jmTvs/1/public/values?alt=json')
      const h2hData = rawData.data.feed.entry.map(e => {
        return {
          id: e.gsx$id.$t,
          serviceType: e.gsx$servicetype.$t,
          serviceProvider: e.gsx$serviceprovider.$t,
          activation: e.gsx$activation.$t,
          crisisType: e.gsx$crisistype.$t,
          projectTitle: e.gsx$projecttitle.$t,
          country: e.gsx$country.$t,
          global: e.gsx$global.$t,
          coordinates: e.gsx$coordinates.$t.length > 0 ? e.gsx$coordinates.$t.split(';').map(x => x.split(',')) : null
        }
      })
      context.commit("setH2HData", h2hData)
    }
  }
});
